* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

body {
  overflow: auto;
}

.search-background {
  height: 60px;
  background-color: #01b4fb;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin: 10px;
  display: flex;
  box-shadow: 2px 5px 10px #0a1509;
}

.search-thumb {
  position: relative;
}

.search-form {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-input {
  height: 30px;
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 5px;
  position: relative;
}

.search-button {
  width: 50px;
  height: 30px;
  border: none;
  border-radius: 0 5px 5px 0;
  padding: 5px;
  transform: translateX(-10px);
}

.search-button:hover {
  background-color: #cecece;
}

.search-icon {
  width: 20px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 15px;
  display: flex;
}

.gallery img {
  height: 200px;
  object-fit: cover;
}

.info {
  gap: 10px;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.photo-card {
  outline: 1px solid #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.load-more {
  width: 100px;
  height: 30px;
  border: none;
  border-radius: 5px;
  margin: 5px auto;
  padding: 5px;
  display: block;
}

.load-more:hover {
  background-color: #cecece;
}

